/* UTILITIES */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: cursive;
    direction: ltr;
  }
  
  a {
    text-decoration: none;
  }
  
  li {
    list-style: none;
  }
  
  /* NAVBAR STYLING STARTS */
  .navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    background-color: #f2ecd3;
    color: #000000;
  }
  
  .nav-links a {
    color: #000000;
  }
  
  /* LOGO */
  .logo {
    max-width: 50%;
    margin-bottom: -20px;
    margin-top: -20px;
  }
  
  /* NAVBAR MENU */
  .menu {
    display: flex;
    gap: 2em;
    font-size: 25px;
    direction: rtl;
  }
  
  .menu li:hover {
    background-color: #d1d0c9;
    border-radius: 5px;
    transition: 0.3s ease;
  }
  
  .menu li {
    padding: 5px 14px;
    direction: rtl;
  }
  
  /* DROPDOWN MENU */
  .services {
    position: relative;
  }
  
  .dropdown {
    background-color: #f2ecd3;
    padding: 1em 0;
    position: absolute;
    display: none;
    border-radius: 8px;
    top: 35px;
  }
  
  .dropdown li+li {
    margin-top: 10px;
  }
  
  .dropdown li {
    padding: 0.5em 1em;
    width: 8em;
    text-align: center;
  }
  
  .dropdown li:hover {
    background-color: #4c9e9e;
  }
  
  .services:hover .dropdown {
    display: block;
  }
  
  /* RESPONSIVE NAVBAR MENU STARTS */
  input[type=checkbox] {
    display: none;
  }
  
  /* HAMBURGER MENU */
  .hamburger {
    display: none;
    font-size: 70px;
    font-weight: 1000;
    user-select: none;
  }
  
  .logos {
    max-width: 30%;
    margin-bottom: -20px;
    margin-top: -20px;
  }
  
  /* APPLYING MEDIA QUERIES */
  @media (max-width: 768px) {
    .logos {
        max-width: 80%;
        height: auto;
      }
    .menu {
      display: none;
      position: absolute;
      background-color: #f2ecd3;
      right: 0;
      left: 0;
      text-align: center;
      padding: 16px 0;
    }
  
    .menu li:hover {
      display: inline-block;
      background-color: #4c9e9e;
      transition: 0.3s ease;
    }
  
    .menu li+li {
      margin-top: 12px;
    }
  
    input[type=checkbox]:checked~.menu {
      display: block;
    }
  
    .hamburger {
      display: block;
      
    }
    
  
    .dropdown {
      left: 50%;
      top: 30px;
      transform: translateX(35%);
    }
  
    .dropdown li:hover {
      background-color: #4c9e9e;
    }
  }
  